:root{
    --primary: white
    
    }

    .btn {
        padding: 8px 20px;
     border-radius: 2px;
     outline: none;
     border: none;
     cursor: pointer;

    }

    .btn--primary{
        background-color: var(--primary);
        color: black;
        border: 1px solid var var(--primary);
    }

    .btn--outline{
        background-color: rgb(192, 120, 120);
        color: rgb(255, 255, 255);
        padding: 8px 20px;
        border: 1px solid var(--primary);
        transition: all 0.3s ease-in-out;
    }

    .btn--medium{
        padding: 8px 20px;
        font-size: 20px;
    }
    .btn--large{
        padding: 12px 26px;
        font-size: 20px;
    }

    .btn--medium:hover, .btn--large:hover{
        background: white;
        color: #242424;
        transition: all 0.3s ease-out;
     }